.listivo_items {

	.listivo_item {
		margin-bottom: rem(60);
	}

	.card_item {
		max-width: 100% !important;
		width: rem(360);
		margin-bottom: rem(60);

		@include tablet {
			width: rem(413);
		}
	}
}

.listivo_item {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;

	width: rem(360);

	&:hover {
		box-shadow: $item_shadow;
	}
	
	@include tablet {
		width: rem(413);
	}

	&-top {
		height: rem(270);
		position: relative;
		z-index: 1;
		border-radius: rem(10);
		overflow: hidden;

		&_cat {
			position: absolute;
			top: rem(28);
			left: rem(28);

			background-color: var(--color3_1);
			border-radius: rem(3);
			color: white;
			font-size: rem(11);
			text-transform: uppercase;
			padding: rem(7) rem(11);
		}
	}

	&-bottom {
		position: relative;
		top: rem(-7);
		padding: rem(42) rem(36);
		margin-bottom: rem(-7);
		border: rem(1) solid #e4ebf0;
		background-color: white;

		border-bottom-left-radius: rem(10);
		border-bottom-right-radius: rem(10);

		.listivo_title {
			font-size: rem(22);
			line-height: rem(30);
			font-weight: 700;
			font-family: 'Comfortaa';
			display: block;
			margin-bottom: rem(22);

			&:hover {
				color: var(--red);
			}

			// @include tablet {
			// 	font-size: rem(22);
			// 	line-height: rem(30);
			// }
		}

		

		.listivo_text {
			color: var(--text-descr);
			line-height: rem(28);
			margin-bottom: rem(32);
		}
	}
}

.listivo_content {
	display: flex;
	flex-wrap: wrap;
	margin-top: rem(100);

	.listivi_item_second {
		margin-bottom: rem(70);

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	// .w-8\/12 {
	// 	padding-right: rem(30);
	// }

	.w-4\/12 {
		padding-left: rem(30);

		.listivo_block_blue,
		.listivo_block_dark,
		.listivo_block_border {
			margin-bottom: rem(46);

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.listivo_field_button {
		margin-bottom: rem(46);
	}

	&-result {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(50);

		&_nav {

			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(36);

					&:last-child {
						margin-right: 0;
					}

					a {
						position: relative;
						color: var(--color2_2);
						font-size: rem(18);
						line-height: rem(48);
						display: block;
						padding-bottom: rem(8);
						min-width: rem(75);
						text-align: center;

						&:hover {
							color: var(--red);

							&::before {
								background: url('data:img/png;base64,') no-repeat;
							}
						}

						&::before {
							content: "";
							position: absolute;
							bottom: 0;
							left: 0;
							width: rem(75);
							height: rem(5);
							background: url('data:img/png;base64,') no-repeat;
							background-size: 100%;
						}
					}

					.listivo_active {
						color: var(--red);

						&::before {
							background: url('data:img/png;base64,') no-repeat;
						}
					}
				}
			}
		}

		.listivo_title {
			font-size: rem(36);
			line-height: rem(46);
			font-weight: 700;
			font-family: $font-title;

			&::after {
				content: "";
				width: rem(117);
				height: rem(6);
				display: block;
				background: url('data:img/png;base64,') no-repeat;
				background-size: 100%;
				margin-top: rem(3);
			}
		}

		&_control {
			display: flex;
			// flex-wrap: wrap;
			align-items: center;

			b {
				font-weight: 500;
				margin-right: rem(24);
				white-space: nowrap;
			}

			.custom-select {
				width: rem(210);
				margin-right: rem(20);
			}

			.listivo_button_icon {
				margin-right: rem(20);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&-table {
		display: flex;
		flex-direction: column;
		width: 100%;
		
		.custom-select,
		.custom-select__option--value {
			height: rem(50);
		}

		.custom-select__option--value,
		.custom-select__dropdown,
		.custom-select--active {
			background-color: #d4eafc !important;
		}

		&_item {
			// margin: 0 rem(48);
			padding: rem(60) rem(48);

			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			&:hover {
				background-color: #f7fdff;
			}

			.w-30 {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				.listivo_image {
					width: rem(200);
					height: rem(140);
					margin-right: rem(30);

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				.listivo_title {
					font-size: rem(20);
					font-weight: 700;
					margin-bottom: rem(22);
					display: block;
				}
			}

			.w-14 {
				display: flex;
				align-items: center;

				.listivo_image {
					width: rem(100);
					height: rem(100);
					border-radius: 50%;
					overflow: hidden;
					margin-bottom: rem(18);

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.listivo_text {
					font-size: rem(18);
				}
				.listivo_yellow_box {
					font-weight: 500;
					background-color: #ffd451;
					padding: rem(18) rem(28);
					border-radius: rem(8);
					height: rem(50);
				}
			}
			.w-14.col {
				flex-direction: column;
				justify-content: center;
				align-items: flex-start;
			}

			

			ul {
				li {
					&:first-child {
						margin-bottom: rem(20);
					}

					margin-bottom: rem(10);

					&:last-child {
						margin-bottom: 0;
					}

					b {
						font-weight: 500;
					}
				}
			}
		}

		&_header {
			background-color: #fff3f4;
			padding: rem(32) rem(48);
			border-radius: rem(10);
			// margin-bottom: rem(58);

			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			
			

			.listivo_sort {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				&-icon {
					display: flex;
					// flex-wrap: wrap;
					flex-direction: column;
					justify-content: space-between;
					height: rem(15);

					i {
						font-size: rem(10);
					}
				}
			}
			
			.listivo_title {
				font-weight: 500;
				text-transform: uppercase;
				margin-right: rem(10);
			}	
		}
	}

}

.listivo_author {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	padding-bottom: rem(22);
	margin-bottom: rem(22);

	border-bottom: rem(1) solid #e4ebf0;

	&-avatar {
		width: rem(50);
		height: rem(50);
		overflow: hidden;
		border-radius: 50%;
		margin-right: rem(10);
	}

	&-item {
		// position: relative;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-right: rem(10);

		&:last-child {
			&::after {
				display: none;
			}
		}

		&::after {
			content: "";
			width: rem(5);
			height: rem(5);
			display: block;
			border-radius: 50%;
			margin-left: rem(10);
			background-color: var(--color6);
		}

		i {
			// font-size: rem(18);
			color: var(--red);
			margin-right: rem(5);
		}

		span {
			font-size: rem(14);
		}
	}

}

.listivi_item_second {
	width: 100%;

	.listivo_item-top {
		height: rem(400);
	}

	.listivo_title {
		font-size: rem(30);
		font-weight: 700;
	}

	.listivo_text {
		border-bottom: rem(1) solid #e4ebf0;
		padding-bottom: rem(32);
	}

	.listivo_author {
		border-bottom: 0;
		margin-bottom: 0;
		padding-bottom: 0;
	}
}

