.listivo_header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin-top: rem(47);
	margin-bottom: rem(42);

	&_logo {
		max-width: rem(124);
		margin-right: rem(90);
	}

	&_nav {
		margin-right: auto;
		display: flex;
		align-items: center;

		> ul {
			display: flex;
			flex-wrap: wrap;

			> li {
				position: relative;
				margin-right: rem(36);

				line-height: rem(56);
				// padding-bottom: rem(14);
				border-top: rem(1) solid transparent;
				transition: all 300ms ease;

				&:last-child {
					margin-right: 0;
				}

				&:hover {
					border-color: var(--red);

					> .listivo_header_nav-dropdown {
						display: block;
					}
				}

				> a {
					display: block;
					font-weight: 500;
					i {
						font-size: rem(14);
						margin-left: rem(12);
					}

					// &:hover {
					// 	color: var(--red);
					// }

					
				}

				.listivo_count {
					top: rem(-2);
					right: rem(-14);
				}
			}
			
		}
	}

	&_right {

		&-control {
			display: flex;
			flex-wrap: wrap;
			

			> ul {
				display: flex;
				flex-wrap: wrap;
				// align-items: center;
				margin-right: rem(22);

				.listivo_drop_dashboard {
					&:hover {

						> a {
							color: white
						}

						.listivo_dashboard_menu {
							display: block;
						}
					}
				}

				> li {
					position: relative;
					display: flex;
					align-items: center;
					margin-right: rem(30);

					&::after {
						content: "";
						position: absolute;
						top: 50%;
						margin-top: rem(-9);
						right: rem(-15);
						height: rem(18);
						width: rem(1);
						background-color: #cccccc;
					}

					&:last-child {
						margin-right: 0;

						&::after {
							display: none;
						}
					}

					.listivo_icon {
						position: relative;
						margin-right: rem(20);

						i {
							font-size: rem(28);
							color: white;
						}
					}

					> a {
						display: flex;
						align-items: center;

						span {
							margin-left: rem(12);
						}

						i {
							font-size: rem(14);
							margin-left: rem(12);
						}

						&:hover {
							color: var(--red);
						}
					}
				}
			}

		}
	}
}

.listivo_header_nav-dropdown {
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 10;
	border-radius: rem(8);
	background-color: white;
	box-shadow: $item_shadow;
	padding: rem(22) 0;
	display: none;

	ul {
		display: flex;
		flex-direction: column;
		li {
			position: relative;
			line-height: rem(45);
			border-bottom: rem(1) solid #dddddd;

			// padding: 0 rem(35);
			margin: 0 rem(35);
			

			&:last-child {
				border-bottom: 0;
			}

			i {
				font-size: rem(12);
			}

			.listivo_header_nav-dropdown {
				top: rem(-22);
				left: 100%;
				margin-left: rem(20);
			}

			&:hover {
				> i {
					color: var(--red);
				}
				> a {
					color: var(--red) !important;
				}

				>.listivo_header_nav-dropdown {
					display: block;
				}

				&::after {
					content: "";
					position: absolute;
					top: rem(10);
					right: rem(-20);
					width: rem(20);
					height: rem(20);
					background-color: transparent;
					display: block;
				}
			}

			a {
				color: var(--text-color) !important;
				display: flex;
				flex-wrap: wrap;
				// width: 100%;
				justify-content: space-between;
				align-items: center;
				white-space: nowrap !important;
			}
		}
	}
}

.listivo_count {
	position: absolute;
	top: rem(-8);
	right: rem(-12);
	width: rem(22);
	height: rem(22);
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 1;

	color: white;
	font-size: rem(12);
}
.listivo_red {
	background-color: var(--red);
}
.listivo_yellow {
	background-color: var(--color3_1);
}
.listivo_purple {
	background-color: var(--color6);
}

.header_bg {
	background-color: var(--color2);
	height: rem(105);
	margin-top: 0;
	margin-bottom: 0;

	ul li a {
		color: white;
	}

	.container-max {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}
}

.listivo_dashboard_menu {
	position: absolute;
	z-index: 11;
	top: rem(58);
	right: rem(-23);
	width: rem(300);
	background-color: white;
	border-radius: rem(8);
	padding: rem(30) rem(20);
	box-shadow: $item_shadow;

	display: none;

	&::before {
		content: "";
		border: solid transparent;
		position: absolute;
		right: rem(20);
		bottom: 100%;
		border-bottom-color: white;
		border-width: rem(8);
		margin-left: 0;
	}

	&-header {
		margin-bottom: rem(18);
		padding-bottom: rem(18);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		border-bottom: rem(1) solid #dddddd;

		.listivo_image {
			width: rem(60);
			height: rem(60);
			border-radius: 50%;
			overflow: hidden;
			margin-right: rem(12);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.listivo_text {
			font-size: rem(20);
			font-weight: 700;
			font-family: $font-title;
		}
	}

	&-nav {
		padding-bottom: rem(18);
		margin-bottom: rem(18);
		border-bottom: rem(1) solid #dddddd;

		&:last-child {
			border-bottom: 0;
			padding-bottom: 0;
			margin-bottom: 0;
		}

		ul {
			display: flex;
			flex-direction: column;

			li {
				margin-bottom: rem(14);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					position: relative;
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;
					color: var(--text-color);
					font-weight: 500;

					&:hover {
						color: var(--red);

						i {
							color: var(--red);
						}

					}

					span {
						margin-right: auto;
					}

					i {
						color: var(--color2);
						font-size: rem(18);
						margin-right: rem(14);
					}

					.listivo_count {
						position: relative;
						top: auto;
						right: auto;
					}
				}
			}
		}
	}
}