.listivo_dashboard {
	margin-bottom: rem(400);

	&-link {
		color: var(--red);
		font-weight: 500;
		text-decoration: underline;
		margin-top: rem(55);
		display: block;

		&:hover {
			text-decoration: none;
		}
	}

	&-image {
		position: relative;
		width: rem(150);
		height: rem(150);
		border-radius: rem(7.5);
		overflow: hidden;

		display: flex;
		align-items: center;
		justify-content: center;

		margin-right: rem(24);

		&:last-child {
			margin-right: 0;
		}

		&::before {
			content: "";
			position: absolute;
			z-index: 1;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0,0,0,.5);
		}

		img {
			position: absolute;
			z-index: 0;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		button {
			position: relative;
			z-index: 2;
			background-color: white;
			width: rem(40);
			height: rem(40);
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;

			margin-right: rem(8);

			&:last-child {
				margin-right: 0;
			}

			i {
				font-size: rem(22);
				color: var(--text-color);
			}
		}
	}

	&-upload {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&_button {
			cursor: pointer;
			width: rem(150);
			height: rem(150);
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #cccccc;
			margin-right: rem(60);
			border-radius: rem(7.5);

			input {
				display: none;
			}
		}

		p {
			font-size: rem(24);
			color: var(--text-descr);
		}
	}

	.listivo_title_block {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		&-right {
			a {
				margin-right: rem(24);

				&:last-child {
					margin-right: 0;
				}
			}

			.listivo_button_l_white {
				height: rem(57);
				border-color: #e4ebf0;

				.listivo_icon {
					height: 100%;
					top: 0;
				}

				&:hover {
					background-color: transparent;
					border-color: #e4ebf0;
					color: var(--text-color);

					.listivo_icon i {
						color: white;
					}
				}

				&:first-child {
					.listivo_icon {
						background-color: var(--color2);

						i {
							color: white;
						}
					}
				}

				&:last-child {
					.listivo_icon {
						background-color: var(--red);

						i {
							color: white;
						}
					}
				}
			}
		}
	}

	&-nav {
		height: rem(100);
		background-color: #edf8fb;
		display: flex;
		align-items: center;
		margin-bottom: rem(50);

		&_item {
			background-color: #d9edf2;
			border-radius: rem(6);
			height: rem(56);
			display: flex;
			align-items: center;
			padding: 0 rem(26);
			margin-right: rem(14);

			&:last-child {
				margin-right: 0;
			}

			&:hover {
				background-color: var(--red);

				i, span {
					color: white;
				}
			}

			i {
				font-size: rem(18);
				margin-right: rem(14);
			}

			span {
				font-weight: 500;
				font-size: rem(18);
			}
		}

		.listivo_active {
			background-color: var(--red);

			i,
			span {
				color: white;
			}
		}
	}

	&-not_listing {
		
		.container-min {
			position: relative;

			&::before {
				content: "";
				position: absolute;
				top: rem(84);
				left: 30%;
				width: rem(29);
				height: rem(29);
				background: url(../img/sun.png) no-repeat;
				background-size: 100%;

				z-index: 1;
			}
		}

		

		.listivo_block_border {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: rem(115) 0;

			&::before {
				content: "";
				position: absolute;
				top: 50%;
				right: 20%;
				width: rem(29);
				height: rem(29);
				background: url(../img/triangle2.png) no-repeat;
				background-size: 100%;
			}

			&::after {
				content: "";
				position: absolute;
				left: rem(100);
				bottom: rem(100);
				width: rem(59);
				height: rem(66);
				background: url(../img/circl1.png) no-repeat;
				background-size: 100%;
			}

			.listivo_title {
				position: relative;
				margin-bottom: rem(124);
				&::after {
					display: none;
				}

				&::before {
					content: "";
					position: absolute;
					left: 50%;
					margin-left: rem(-32);
					bottom: rem(-100);
					width: rem(55);
					height: rem(41);
					background: url(../img/arrow_blue.png) no-repeat;
					background-size: 100%;

					transform: scaleY(-1);
				}
			}

			.listivo_button_default {
				text-transform: uppercase;
			}
		}
	}

	&-content {
		.custom-select {
			width: rem(230);
		}
		.listivo_field_button {
			width: rem(300);
			margin-bottom: 0;

			button {
				background-color: #edf8fb;
			}
		}

		.listivo_content {
			margin-top: 0;
		}

		.listivo_items {
			.card_item:last-child {
				margin-bottom: 0;
			}
		}

		.listivo_listings_i {
			.card_item-top {
				&::before {
					display: none;
				}
			}
		}
		
	}

	&-message {
		.container-min {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

		}

		.w-7\/12 {
			padding-left: rem(56);
		}

		&_header {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			margin-bottom: rem(44);

			.listivo_title {
				font-size: rem(36);
				font-weight: 700;
				font-family: $font-title;
			}
		}

		&_convas {
			background-color: #f7fdff;
			padding: rem(44);
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;

			.listivo_message_button_red {
				align-self: center;
				margin-bottom: rem(42);
			}

			.listivo_message_group {
				margin-bottom: rem(38);
				display: flex;
				flex-wrap: wrap;
				flex-direction: column;
			}

			.listivo_message_right_item {
				align-self: flex-end;

				font-size: rem(14);
				color: white;
				background-color: var(--color2);
				border-radius: rem(7);
				margin-bottom: rem(7);
				padding: rem(14) rem(20);

				a {
					text-decoration: underline;
				}

			}

			.listivo_message_left_item {
				align-self: flex-start;

				display: flex;

				align-items: flex-end;
				margin-bottom: rem(7);

				img {
					border-radius: 50%;
					width: rem(45);
					height: rem(45);
					object-fit: cover;
					margin-right: rem(16);
				}

				p {
					max-width: rem(320);
					
					font-size: rem(14);
					color: var(--text-color);
					background-color: #def6fc;
					border-radius: rem(7);
					
					padding: rem(14) rem(20);

					&:not(:last-child) {
						margin-bottom: rem(7);
					}

					a {
						text-decoration: underline;
					}
					
				}
			}

			.listivo_message_date {
				align-self: center;

				font-size: rem(12);
				color: #cccccc;
				margin-bottom: rem(32);
			}
		}

		&_add {
			background-color: #edf8fb;
			padding: rem(44);
			display: flex;
			flex-direction: column;

			.listivo_title {
				font-size: rem(24);
				font-weight: 700;
				font-family: $font-title;
				margin-bottom: rem(28);
			}

			.listivo_field {
				margin-bottom: rem(26);
			}

			.listivo_button {
				align-self: flex-end;

				.listivo_icon {
					width: rem(45);
				}
			}
		}

		&_people {
			padding: rem(24) rem(28);
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			cursor: pointer;

			&:hover {
				background-color: #edf8fb;
			}

			.listivo_image {
				width: rem(70);
				height: rem(70);
				border-radius: 50%;
				overflow: hidden;
				margin-right: rem(24);
			}

			.listivo_info {

				.listivo_title {
					font-size: rem(20);
					font-weight: 700;
					font-family: $font-title;
					margin-bottom: rem(8);
				}

				span {
					font-size: rem(12);
					color: var(--text-descr);
				}
			}
		}

		.listivo_active {
			background-color: #edf8fb;
		}
	}
}

.listivo_message_button_red {
	font-size: rem(14);
	font-weight: 500;
	color: white;
	background-color: var(--red);
	border-radius: rem(8);
	padding: rem(12) rem(18);
}


.listivo_switch_field {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	span {
		font-size: rem(24);
		font-weight: 500;

		&:first-of-type {
			order: -1;
		}

		&:last-of-type {
			order: 2;
		}
	}
	input {
		display: none;

		&:checked {
			~ span:last-of-type {
				color: var(--red);
			}

			~ label .listivo_switch_field-toggle {
				&::before {
					transform: translateX(rem(44));
				}
			}
		}
		&:not(:checked) {
			~span:first-of-type {
				color: var(--red);
			}
		}
	}

	label {
		cursor: pointer;
	}

	&-toggle {
		position: relative;
		border-radius: rem(22);
		background-color: white;
		box-shadow: $item_shadow;
		width: rem(82);
		height: rem(45);
		display: flex;
		align-items: center;
		margin: 0 rem(28);

		order: 1;

		&::before {
			content: "";
			width: rem(29);
			height: rem(29);
			border-radius: 50%;
			background-color: var(--red);
			transition: all 300ms ease;

			transform: translateX(rem(8));
		}
	}
}

.listivo_pay_select {
	margin-bottom: rem(70);
	display: flex;
	justify-content: center;
}

.listivo_pay_item {
	background-color: white;
	box-shadow: $item_shadow;
	border-radius: rem(10);

	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	padding: 0 rem(58);

	margin-bottom: rem(34);

	border: rem(2) solid white;

	&:last-of-type {
		margin-bottom: 0;
	}

	&-left {
		position: relative;
		padding: rem(54) rem(150) rem(54) 0;
		height: 100%;
		width: rem(250);
		border-right: rem(1) solid #dddddd;

		display: flex;
		flex-direction: column;

		@include tablet {
			width: rem(340);
		}

		.listivo_yellow_box {
			position: absolute;
			top: rem(-17);

			text-transform: uppercase;
			color: white;
			font-size: rem(12);
			font-weight: 700;
			background-color: var(--color3_1);
			padding: rem(8) rem(20);
			border-radius: rem(5);
		}

		span {
			display: block;
			margin-bottom: rem(5);
		}

		b {
			display: block;
			font-weight: 700;
			color: var(--red);
			font-size: rem(36);
			font-family: $font-title;
		}
	}

	&-center {
		margin-right: auto;
		padding-left: rem(42);

		ul {
			li {
				margin-bottom: rem(14);
				display: flex;
				flex-wrap: wrap;

				&:last-child {
					margin-bottom: 0;
				}

				span {
					width: rem(100);
					font-weight: 300;
					color: var(--text-descr);
				}

				b {
					font-weight: 500;
				}
			}
		}
	}
}
.listivo_pay_item.listivo_active {
	border-color: var(--red);
}
.listivo_pay_item-free {
	border-color: rgba(83,163,193,.2);
	box-shadow: none;
	.listivo_pay_item-left {
		padding: rem(38) rem(150) rem(38) 0;
		border-right-color: #d2e9f0;

		b {
			color: var(--text-color);
		}
	}
	
}

.listivo_checked_bord_field {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;

	input {display: none;}

	label {
		cursor: pointer;
		width: rem(50);
		height: rem(50);
		border-radius: 50%;
		border: rem(2) solid #e4ebf0;
		transition: all 300ms ease;
	}

	.check {
		position: absolute;
		pointer-events: none;
		top: rem(12);
		left: rem(12);
		z-index: 1;
		width: rem(26);
		height: rem(26);
		border-radius: 50%;
		background-color: var(--red);
		transition: all 300ms ease;
		opacity: 0;
	}

	input:checked {
		~ .check {
			opacity: 1;
		}
		~ label {
			border-color: var(--red);
		}
	}
}

.mb-150 {
	margin-bottom: rem(150);
}

.listivo_table_lite {
	display: flex;
	flex-direction: column;

	&-header,
	&-item {
		padding: rem(26) 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		border-bottom: rem(1) solid var(--red);

		.listivo_checked_bord_field {
			label {
				width: rem(26);
				height: rem(26);
			}

			.check {
				width: rem(12);
				height: rem(12);
				top: rem(7);
				left: rem(7);
			}
		}

		.listivo_first {
			width: 70%;
			display: flex;
			align-items: center;
		}

		.listivo_first-top {
			align-items: flex-start;
			padding: rem(34) 0;
			
			&::before {
				display: none;
			}

			.listivo_checked_bord_field {
				margin-right: rem(12);
			}

			.listivo_first-text {
				display: flex;
				flex-direction: column;
				padding-top: rem(3);

				.listivo_title {
					font-size: rem(20);
					font-weight: 700;
					margin-bottom: rem(20);
				}
				.listivo_text {
					line-height: rem(28);
					color: var(--text-descr);
				}

				> .flex {
					.listivo_title {
						margin-bottom: 0;
						margin-right: rem(24);
					}
				}

				.listivo_pay_icons {
					display: flex;
					flex-wrap: wrap;
					align-items: center;

					img {
						margin-right: rem(16);

						&:last-child {
							margin-right: 0;
						}
					}
				}
			}
		}

		.listivo_last {
			width: 30%;
			padding-left: rem(36);
		}

		b {
			font-weight: 500;
			text-transform: uppercase;
		}
	}

	&-item {
		border-color: #dddddd;

		.listivo_first {
			width: 70%;

			position: relative;

			&::before {
				content: "";
				position: absolute;
				right: 0;
				width: rem(1);
				height: rem(40);
				background-color: #dddddd;
			}
		}
	}

	&-item.last {
		border-bottom: 0;
		padding-bottom: 0;
	}
}

.listivo_pay_end {

	.listivo_button {
		text-transform: uppercase;
	}

	.listivo_white_block {
		margin-left: rem(100);
		margin-right: rem(100);
		display: flex;
		flex-direction: column;
		align-items: center;

		.listivo_title {
			span {
				color: var(--red);
			}
		}

		h3.listivo_title {
			font-weight: 700;
			font-family: inherit;
			margin-bottom: rem(55);
		}

		.listivo_subtitle {
			width: rem(650);
			display: flex;
			justify-content: center;
			padding: rem(26) 0;

			font-size: rem(18);

			border-top: rem(1) solid #e4ebf0;
			border-bottom: rem(1) solid #e4ebf0;

			margin-bottom: rem(45);

			span {
				color: var(--color2);
				margin-left: rem(4);
			}
		}
	}
}

.listivo_pay_table {
	width: 100%;
	margin-bottom: rem(55);

	&:last-child {
		margin-bottom: 0;
	}
	
	&-title {
		margin-bottom: rem(18);
		margin-left: rem(6);
	}

	.listivo_block_border {
		padding: rem(38);

		ul {
			margin-bottom: rem(28);
			&:last-child {
				margin-bottom: 0;
			}
			li {
				&:first-child {
					margin-bottom: rem(12);
				}

				&:last-child {
					font-size: rem(18);
					font-weight: 600;
				}
			}
		}
	}
}