#listing_search_map {
	.listivo_listing_filter-top .container-min::after,
	.listivo_listing_filter-top .container-min::before {
		display: none;
	}

	.w-65 {
		// width: 65%;
		grid-column: 2 / span 1;
	}

	.listivo_search_map {
		// width: 35%;
		grid-column-end: 2 span;
		position: relative;
		height: rem(900);

		img {
			pointer-events: none;
			position: absolute;
			z-index: -1;
			top: 0;
			left: 0;
			width: 100%;
			// height: 100%;
			object-fit: cover;
		}

		&-header {
			position: absolute;
			top: rem(26);
			left: 50%;
			margin-left: rem(-90);
			font-size: rem(14);
			font-weight: 500;
			background-color: white;
			box-shadow: 0 0 rem(27) rem(2.4) rgba(5, 35, 44, .13);
			border-radius: rem(5);
			padding: rem(7);

			display: flex;
			align-items: center;

			.listivo_icon {
				margin-right: rem(10);
				background-color: var(--red);
				border-radius: rem(5);
				width: rem(24);
				height: rem(24);
				display: flex;
				align-items: center;
				justify-content: center;

				i {
					font-size: rem(14);
					color: white;
				}
			}
		}

		&-item {
			position: absolute;
			cursor: pointer;
			height: rem(34);
			display: flex;
			align-items: center;
			border-radius: rem(5);
			padding: 0 rem(17);
			font-size: rem(18);
			font-weight: 600;
			background-color: white;
			box-shadow: 0 0 rem(27) rem(2.4) rgba(5, 35, 44, .13);

			&:hover {
				background-color: var(--color2);
				color: white;
				z-index: 10 !important;
			}
		}

		&-item.listivo_active {
			background-color: var(--color2);
			color: white;
			z-index: 10 !important;
		}

		&-box {

			position: absolute;
			top: rem(166);
			right: rem(200);
			z-index: 2;

			display: flex;
			flex-direction: column;
			border-radius: rem(10);
			overflow: hidden;
			width: rem(250);
			&_top {
				position: relative;
				height: rem(150);
				
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

				span {
					position: absolute;
					top: rem(18);
					left: rem(18);
					background-color: var(--color3_1);
					color: white;
					font-weight: 500;
					font-size: rem(11);
					border-radius: rem(5);
					padding: rem(7) rem(11);
				}

				button {
					position: absolute;
					top: rem(18);
					right: rem(18);
					border-radius: 50%;
					width: rem(32);
					height: rem(32);
					background-color: white;

					display: flex;
					align-items: center;
					justify-content: center;

					i {
						color: var(--text-color);
					}
				}
			}

			&_bottom {
				padding: rem(16) 0;
				background-color: white;
				text-align: center;

				span {
					font-weight: 600;
					font-size: rem(16);
					display: block;
					margin-bottom: rem(12);
				}
				b {
					font-size: rem(20);
					color: var(--red);
					font-weight: 700;
					font-family: $font-title;
				}
			}
		}
	}

	>.container-fluid {
		display: grid;
		grid-template-columns: calc((100% - 1920px)/2) 6fr 4fr calc((100% - 1920px)/2);

		.container-fluid {
			padding: 0 rem(55);
		}
	}

	.listivo_listing_filter {
		.container-fluid {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}
	}

	.listivo_field_box:last-child .listivo_field {
		width: 75%;
	}

	.listivo_listing_filter-top .listivo_field_box:last-child .custom-select {
		width: 25%;
	}

	@include tablet {
		.listivo_field_box:last-child .listivo_field {
			width: 70%;
		}

		.listivo_listing_filter-top .listivo_field_box:last-child .custom-select {
			width: 30%;
		}
		
		.card_item-bottom_head {
			padding: rem(32) rem(22) rem(20) rem(22);
		}

		.listivo_items-listview .card_item-bottom .listivo_text {
			padding: 0 rem(22);
			margin-bottom: rem(20);
		}

		
	}

	
}